$color: #ffebef;
$breakpoint-tablet: 768px;

.card {
  background-color: $color;
  border-radius: 0.25rem;

  @media (min-width: $breakpoint-tablet) {
    max-width: 20rem;
  }

  .cardContent {
    padding: 1rem;
  }
  .cardFooter {
    padding: 0rem 1rem 1rem;
    text-align: right;
  }
}
